.big-button {
  background-color: $purple;
  border: none;
  border-bottom: 0.6rem solid darken($purple, 10%);
  color: #fff;
  font-weight: bold;
  font-size: $l-size;
  margin-bottom: $l-size;
  padding: 2.4rem;
  outline: none;
  width: 100%;
}
.big-button:disabled {
  opacity: 0.5;
}

.button {
  background-color: $purple;
  border: none;
  border-bottom: 0.3rem solid darken($purple, 10%);
  color: #fff;
  font-weight: 500;
  padding: $s-size;
  outline: none;
}
.button__link {
  background: none;
  border: none;
  color: $off-white;
  padding: 0;
}
@media (min-width: $desktop-breakpoint) {
  .big-button {
    margin-bottom: $xl-size;
  }
} ;
